/* ContactMePage.css */

.profile-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 40px;
}

.profile-image {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 50%;
  margin-bottom: 20px;
}

.profile-name {
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 10px;
}

.profile-email {
  color: #777;
  font-size: 16px;
}

.contact-info {
  margin-top: 40px;
  text-align: center;
}

.contact-info p {
  margin: 10px 0;
}
