.header-container {
  background-color: #333;
  color: #fff;
  padding: 20px;
  text-align: center;
}

.header-title {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 24px;
  margin: 0;
}

.header-menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header-menu li {
  display: inline-block;
  margin-right: 10px;
}

.header-menu li a {
  color: #fff;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 16px;
  padding: 5px 10px;
}
