.intro-container {
  position: relative;
  text-align: center;
  padding-top: 60px;
  padding-bottom: 120px;
  background-color: #f2f2f2;
  overflow: hidden;
}

.intro-container img {
  width: 100%;
  max-width: 400px;
  height: auto;
  opacity: 0.7;
  border-radius: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.intro-container:before {
  content: "";
  position: absolute;
  top: 80px; /* Adjust the top value to match the height of the header */
  width: 100%;
  max-width: 400px;
  height: calc(100% - 220px); /* Subtract the total height of the header and footer */
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
  z-index: 1;
  border-radius: 20px;

}



.intro-container h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 36px;
  margin: 0;
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  padding: 20px;
  z-index: 2;
}

.intro-container h1 span {
  display: block;
}

footer {
  background-color: #333;
  color: #fff;
  padding: 20px;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 14px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 3;
}
